@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 800;
  src: url("../fonts/raleway/raleway-800.woff2");
  font-display: swap;
}
@font-face {
  font-family: "Rubik";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/rubik/rubik-400.woff2");
  font-display: swap;
}
@font-face {
  font-family: "Rubik";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/rubik/rubik-700.woff2");
  font-display: swap;
}
@font-face {
  font-family: "Dancing";
  font-style: normal;
  font-weight: 800;
  src: url("../fonts/dancing/DancingScript-Bold.woff2");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
*:focus {
  outline: none;
}
html,
body {
  margin: 0;
  padding: 0;
}
html {
  font-family: "Raleway", "Arial", sans-serif;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}
body {
  width: 100vw;
  max-width: 1600px;
  margin: 0 auto;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto 1fr auto;
  grid-template-areas: "header" "main" "footer";
  overflow-x: hidden;
  position: relative;
}
.main {
  grid-area: main;
  height: 100%;
}
.header {
  grid-area: header;
}
footer {
  grid-area: footer;
}
a {
  text-decoration: none;
  color: #d6d6d6;
}
button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none;
  display: block;
}
ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}
h1,
h2,
h3,
h4 {
  margin: 0;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  width: 100%;
  margin: 0 auto;
}
.layout-wrapper {
  padding: 0 17px 0 17px;
}
@media (min-width: 768px) {
  .layout-wrapper {
    padding: 0 30px 0 30px;
  }
}
@media (min-width: 1200px) {
  .layout-wrapper {
    padding: 0 60px 0 60px;
  }
}
.modal-opened {
  overflow: hidden;
}
.header {
  display: flex;
  justify-content: space-between;
  font-family: "Rubik", "Arial", sans-serif;
  font-weight: 700;
  padding-top: 32px;
}
.name {
  width: 20%;
}
.page-nav ul {
  display: flex;
  justify-content: space-between;
}
.page-nav li {
  margin-left: 75px;
  margin-bottom: 20px;
}
.page-nav a {
  padding: 5px;
}
.page-nav a:hover,
.page-nav a:focus,
.name a:hover,
.name a:focus {
  color: #ffffff;
  border-bottom: 2px solid #ffffff;
}
.page-nav a:active,
.name a:active {
  opacity: 0.5;
}
.themebutton {
  display: block;
  background-color: transparent;
  width: 20px;
  height: 20px;
  border: none;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100%;
  cursor: pointer;
  position: relative;
}
.themebutton:active {
  opacity: 0.5;
}
.popup {
  display: none;
}
.popup-light {
  display: none;
}
.hero {
  display: flex;
  flex-direction: column;
  height: 100vh;
  align-items: center;
  justify-content: center;
  position: relative;
  font-family: "Rubik", "Arial", sans-serif;
  font-weight: 700;
  margin-bottom: 350px;
}
.hero div {
  margin: 0 auto;
  text-align: center;
}
.hero__wowanderful {
  font-family: "Dancing", "Arial", sans-serif;
  font-weight: 800;
  font-size: 68px;
  line-height: 54px;
  position: absolute;
  top: 80px;
  width: 100%;
  height: 80px;
  color: tomato;
  /* Fallback: assume this color ON TOP of image */
}
.hero-first-p {
  display: flex;
  flex-direction: column;
}
.hero__whatis {
  font-size: 52px;
  line-height: 30px;
  margin-bottom: 45px;
}
.hero-name {
  font-size: 56px;
  line-height: 30px;
  margin-bottom: 45px;
}
.hero__whoiam {
  font-size: 38px;
  line-height: 30px;
}
.hero__wherebased {
  font-size: 18px;
  line-height: 30px;
}
.dark .hero__introduction {
  color: white;
  /* Fallback: assume this color ON TOP of image */
  background-image: url(../../img/granit.jpg);
  background-size: 100%;
  background-position: center;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.arrow-active {
  position: absolute;
  bottom: 100px;
  width: 70px;
  height: 70px;
  left: calc(50% - 35px);
  transform: rotate(45deg);
  animation: blinking-arrow 2s infinite;
}
.dark .arrow-active {
  border: 6px solid #ffffff;
  border-left: transparent;
  border-top: transparent;
}
@keyframes blinking-arrow {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.projects {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  margin-bottom: 400px;
  position: relative;
}
.project__list {
  display: grid;
  grid-template-columns: 300px 300px 300px;
  grid-template-rows: auto;
  grid-gap: 30px;
}
.project__link {
  position: relative;
}
.project__image-container {
  width: 300px;
  height: 180px;
  background-color: #8c8c8c;
}
.project__link p {
  text-align: center;
}
.project__image-container:hover {
  position: absolute;
  width: 320px;
  height: 190px;
  top: 0;
  left: -15px;
}
.upbutton {
  position: absolute;
  display: block;
  font-size: 28px;
  width: 80px;
  height: 25px;
  padding: 5px;
  padding-left: 35px;
  padding-top: 0;
  padding-bottom: 10px;
  background-color: #d6d6d6;
  color: black;
  top: 25px;
  left: 15px;
  text-transform: uppercase;
  background-image: url("../../img/svg/up.svg");
  background-repeat: no-repeat;
  background-position: 4px 1px;
  background-size: 31%;
}
.upbutton:hover {
  background-color: tomato;
}
.upbutton:active {
  background-color: white;
}
.arrowup {
  position: absolute;
  top: 50px;
  width: 70px;
  height: 70px;
  left: 40px;
  transform: rotate(-45deg);
}
.about {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  margin-bottom: 300px;
  position: relative;
}
.about-container {
  display: grid;
  grid-template-columns: 25% 75%;
}
.about__image-container {
  width: 140px;
  height: 140px;
  border-radius: 50%;
  background-color: tomato;
  border: 4px tomato solid;
  overflow: hidden;
}
.about__text-block {
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: start;
  text-align: start;
}
.about__name {
  font-size: 32px;
  margin-bottom: 30px;
  color: tomato;
}
.about__skills,
.about__soft,
.about__tech {
  margin-bottom: 30px;
}
.skills__name,
.soft__name,
.tech__name {
  font-size: 28px;
}
.about__download {
  align-self: center;
  text-transform: uppercase;
}
.about__download a {
  background-image: url("../../img/svg/save-icon.svg");
  background-repeat: no-repeat;
  background-position-x: 0%;
  width: 250px;
  display: block;
  padding: 5px;
  padding-left: 50px;
}
.about__download a:hover {
  color: #ffffff;
}
.about__download:active {
  opacity: 0.5;
}
.payment {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  margin-bottom: 400px;
}
.payment::before {
  position: absolute;
  content: "";
  width: 150px;
  height: 150px;
  top: 130px;
  left: calc(50% - 75px);
  background-image: url("../../img/svg/computer-laptop-icon.svg");
  background-repeat: no-repeat;
  background-position: center center;
}
.payment::after {
  position: absolute;
  content: "";
  width: 120px;
  height: 120px;
  bottom: 130px;
  left: calc(50% - 60px);
  background-image: url("../../img/svg/wallet-icon.svg");
  background-repeat: no-repeat;
  background-position: center center;
}
.payment-wrapper {
  display: grid;
  grid-template-columns: 280px 280px;
  grid-row-gap: 45px;
}
.payment__description {
  grid-column: 1 / 3;
  font-size: 28px;
  text-align: center;
}
.firstlink,
.secondlink {
  font-size: 20px;
}
.secondlink {
  text-align: right;
}
.main {
  display: flex;
  flex-direction: column;
}
.footer {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
}
.footer ul {
  display: grid;
  grid-template-columns: 80px 80px;
  grid-gap: 40px;
}
.footer ul a {
  display: block;
  width: 60px;
  height: 60px;
  background-repeat: no-repeat;
  background-position: center center;
}
.discord {
  background-image: url("../../img/svg/discord-white-icon-grey.svg");
}
.telegram {
  background-image: url("../../img/svg/telegram-white-icon-grey.svg");
}
.whatsapp {
  background-image: url("../../img/svg/whatsapp-white-icon-grey.svg");
}
.footer-email {
  background-image: url("../../img/svg/at-the-rate-icon-grey.svg");
}
.discord:hover {
  background-image: url("../../img/svg/discord-icon-tomato.svg");
}
.telegram:hover {
  background-image: url("../../img/svg/telegram-icon-tomato.svg");
}
.whatsapp:hover {
  background-image: url("../../img/svg/whatsapp-icon-tomato.svg");
}
.footer-email:hover {
  background-image: url("../../img/svg/at-the-rate-icon-tomato.svg");
}
.discord:active {
  background-image: url("../../img/svg/discord-white-icon.svg");
}
.telegram:active {
  background-image: url("../../img/svg/telegram-white-icon.svg");
}
.whatsapp:active {
  background-image: url("../../img/svg/whatsapp-white-icon.svg");
}
.footer-email:active {
  background-image: url("../../img/svg/at-the-rate-icon.svg");
}
.footer-logo {
  line-height: 54px;
  position: absolute;
  bottom: 80px;
  width: 200px;
  display: flex;
  justify-content: center;
  left: calc(50% - 100px);
}
.footer-logo a {
  font-family: "Dancing", "Arial", sans-serif;
  font-weight: 800;
  font-size: 28px;
  text-align: center;
  color: #d6d6d6;
  margin: 0;
}
.footer-logo a:hover {
  color: tomato;
}
.footer-logo a:active {
  color: #ffffff;
}
.dark {
  background-color: #111111;
  color: #d6d6d6;
}
.dark .themebutton {
  background-image: url("../../img/svg/day-sunny-icon.svg");
}
.dark .themebutton:hover {
  background-image: url("../../img/svg/day-sunny-white.svg");
}
.dark .themebutton:hover + .popup {
  display: block;
  position: absolute;
  width: 105px;
  height: 25px;
  padding: 0;
  background-color: #cfcfcf;
  color: #111111;
  font-size: 12px;
  top: 64px;
  left: 481px;
  border-radius: 5%;
}
.dark .projects h2 {
  font-size: 52px;
  line-height: 73px;
  margin-bottom: 45px;
  color: white;
  background-image: url(../../img/granit.jpg);
  background-size: 100%;
  background-position: center;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.dark .arrowup {
  border: 6px solid #ffffff;
  border-left: transparent;
  border-bottom: transparent;
}
.dark .about h2 {
  font-size: 52px;
  line-height: 73px;
  margin-bottom: 45px;
  color: white;
  background-image: url(../../img/granit.jpg);
  background-size: 100%;
  background-position: center;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-top: 30px;
}
.light {
  background-color: #ffffff;
  color: #63dc90;
}
.light a {
  color: #63dc90;
}
.light a:hover {
  color: #00b945;
}
.light .hero__introduction {
  color: #00b945;
}
.light .projects h2,
.light .about h2 {
  font-size: 52px;
  line-height: 73px;
  margin-bottom: 45px;
  color: #00b945;
}
.light .themebutton {
  background-image: url("../../img/svg/moon-icon.svg");
}
.light .themebutton:hover {
  background-image: url("../../img/svg/moon-icon-hover.svg");
}
.light .themebutton:hover ~ .popup-light {
  display: block;
  position: absolute;
  width: 105px;
  height: 25px;
  padding: 0;
  background-color: #cfcfcf;
  color: #111111;
  font-size: 12px;
  top: 64px;
  left: 481px;
  border-radius: 5%;
}
.light .arrow-active {
  border: 6px solid tomato;
  border-left: transparent;
  border-top: transparent;
}
.light .arrowup {
  border: 6px solid tomato;
  border-left: transparent;
  border-bottom: transparent;
}
