.about {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 100vh;
    margin-bottom: 300px;
    position: relative;
}

.about-container {
    display: grid;
    grid-template-columns: 25% 75%;
}

.about__image-container {
    width: 140px;
    height: 140px;
    border-radius: 50%;
    background-color: tomato;
    border: 4px tomato solid;
    overflow: hidden;
}

.about__text-block {
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: start;
    text-align: start;
}

.about__name {
    font-size: 32px;
    margin-bottom: 30px;
    color: tomato;
}

.about__skills, .about__soft, .about__tech {
    margin-bottom: 30px;
}

.skills__name, .soft__name, .tech__name {
    font-size: 28px;
}

.about__download {
    align-self: center;
    text-transform: uppercase;
}

.about__download a {
    background-image: url("../../img/svg/save-icon.svg");
    background-repeat: no-repeat;
    background-position-x: 0%;
    width: 250px;
    display: block;
    padding: 5px;
    padding-left: 50px;
}

.about__download a:hover {
    color: #ffffff;
}

.about__download:active {
    opacity: 0.5;
}