.dark {
    background-color: #111111;
    color: @main-color;

    .themebutton {
        background-image: url("../../img/svg/day-sunny-icon.svg");
   
        &:hover {
           background-image: url("../../img/svg/day-sunny-white.svg");
        }
   }

   .themebutton:hover + .popup {
        display: block;
        position: absolute;
        width: 105px;
        height: 25px;
        padding: 0;
        background-color: #cfcfcf;
        color: #111111;
        font-size: 12px;
        top: 64px;
        left: 481px;
        border-radius: 5%;
    }

    .projects h2 {
        font-size: 52px;
        line-height: 73px;
        margin-bottom: 45px;
        color: white;
        background-image: url(../../img/granit.jpg);
        background-size: 100%;
        background-position: center;
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }

    .arrowup {
        border: 6px solid #ffffff;
        border-left: transparent;
        border-bottom: transparent;
    }

    .about h2 {
        font-size: 52px;
        line-height: 73px;
        margin-bottom: 45px;
        color: white;
        background-image: url(../../img/granit.jpg);
        background-size: 100%;
        background-position: center;
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        margin-top: 30px;
    }

    
  }