.projects {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    margin-bottom: 400px;
    position: relative;
}

.project__list {
    display: grid;
    grid-template-columns: 300px 300px 300px;
    grid-template-rows: auto;
    grid-gap: 30px;
}

.project__link {
    position: relative
}

.project__image-container {
    width: 300px;
    height: 180px;
    background-color: #8c8c8c;
}

.project__link p {
    text-align: center;
}

.project__image-container:hover {
    position: absolute;
    width: 320px;
    height: 190px;
    top: 0;
    left: -15px;
}

.upbutton {
    position: absolute;
    display: block;
    font-size: 28px;
    width: 80px;
    height: 25px;
    padding: 5px;
    padding-left: 35px;
    padding-top: 0;
    padding-bottom: 10px;
    background-color: @main-color;
    color: black;
    top: 25px;
    left: 15px;
    text-transform: uppercase;
    background-image: url("../../img/svg/up.svg");
    background-repeat: no-repeat;
    background-position: 4px 1px;
    background-size: 31%;

    &:hover {
        background-color: tomato;
    }

    &:active {
        background-color: white;
    }
}

.arrowup {
    position: absolute;
    top: 50px;
    width: 70px;
    height: 70px;
    left: 40px;

    transform: rotate(-45deg);
}

