.hero {
    display: flex;
    flex-direction: column;
    height: 100vh;
    align-items: center;
    justify-content: center;
    position: relative;
    font-family: "Rubik", "Arial", sans-serif;
    font-weight: 700;
    margin-bottom: 350px;
}

.hero div {
    margin: 0 auto;
    text-align: center;
}


.hero__wowanderful {
    font-family: "Dancing", "Arial", sans-serif;
    font-weight: 800;
    font-size: 68px;
    line-height: 54px;
    position: absolute;
    top: 80px;
    width: 100%;
    height: 80px;
    color: tomato;  /* Fallback: assume this color ON TOP of image */
    // background-image: url(../../img/granit.jpg);
    // background-size: 100%;
    // background-position: center;
    // background-clip: text;
    // -webkit-background-clip: text;
    // -webkit-text-fill-color: transparent;

}

.hero-first-p {
    display: flex;
    flex-direction: column;
}

.hero__whatis {
    font-size: 52px;
    line-height: 30px;
    margin-bottom: 45px;
}

.hero-name {
    font-size: 56px;
    line-height: 30px;
    margin-bottom: 45px;
}

.hero__whoiam {
    font-size: 38px;
    line-height: 30px;
}

.hero__wherebased {
    font-size: 18px;
    line-height: 30px;
}

.dark .hero__introduction {
   color: white;  /* Fallback: assume this color ON TOP of image */
   background-image: url(../../img/granit.jpg);
   background-size: 100%;
   background-position: center;
   background-clip: text;
   -webkit-background-clip: text;
   -webkit-text-fill-color: transparent;
}

.arrow-active {
    position: absolute;
    bottom: 100px;
    width: 70px;
    height: 70px;
    left: calc(50% - 35px);
    
    transform: rotate(45deg);
    animation: blinking-arrow 2s infinite;
}

.dark .arrow-active {
    border: 6px solid #ffffff;
    border-left: transparent;
    border-top: transparent;
}

@keyframes blinking-arrow {
    0% {
        opacity: 1;
    }
    50% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}