.header {
    display: flex;
    justify-content: space-between;
    font-family: "Rubik", "Arial", sans-serif;
    font-weight: 700;
    padding-top: 32px;

}

.name {
    width: 20%;
}

.page-nav ul {
    display: flex;
    justify-content: space-between;
}

.page-nav li {
    margin-left: 75px;
    margin-bottom: 20px;
}

.page-nav a {
    padding: 5px;
}

.page-nav a:hover, 
.page-nav a:focus,
.name a:hover,
.name a:focus {
    color: #ffffff;
    border-bottom: 2px solid #ffffff;
}

.page-nav a:active,
.name a:active {
    opacity:0.5;
}

.themebutton {
    display: block;
    background-color: transparent;
    width: 20px;
    height: 20px;
    border: none;
   
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 100%;
    cursor: pointer;
    position: relative;

    &:active {
        opacity: 0.5;
    }
}

.popup {
    display: none;
}

.popup-light {
    display: none;
}




